*,*::after,*::before{margin:0;box-sizing:border-box;outline:none}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.wrapper{margin:20px auto;max-width:90%}.chat{display:flex;height:calc(100vh - 40px);border:1px solid rgba(159,183,197,0.2);border-radius:8px;overflow:hidden}.chat-window{flex:1}.chat-window__messages{display:flex;flex-direction:column;justify-content:space-between;flex:1;padding:30px;height:100%}.chat-window__messages-wrapper{flex:1;height:calc(100% - 155px);overflow:auto}.message{margin-bottom:20px;padding-bottom:10px}.message:not(:last-child){border-bottom:1px solid rgba(100,100,0,0.2)}.message__text{display:inline-flex;padding:8px 12px 12px;margin-bottom:2px;border-radius:6px;border-top:1px solid rgba(100,100,0,0.2);background-color:rgba(113,96,255,0.75);color:#fff}.message__user-container{display:flex;align-items:center;justify-content:space-between}.message__user-name{font-size:14px;opacity:0.7}.message__user-name--small{font-size:10px;opacity:0.5}.form{position:relative;margin-top:20px;padding-top:20px;border-top:1px solid rgba(100,100,0,0.2)}.form__control{padding:12px 16px;padding-right:110px;width:100%;border:1px solid rgba(100,100,0,0.3);border-radius:6px}.form__control:focus{border:1px solid #556CFF}.form__control:focus+.form__button{border-left:1px solid #556CFF}.form__button{position:absolute;top:20px;right:0;bottom:0;padding:0;width:100px;border:none;border-left:1px solid rgba(100,100,0,0.3);font-size:14px;font-weight:500;background-color:transparent;cursor:pointer;opacity:0.7}.log-in{margin:100px auto 0;width:300px}.log-in__name-field{margin-bottom:10px;padding:16px 12px;width:100%;border:1px solid #556CFF;border-radius:6px;font-size:18px;color:#000}.log-in__button{padding:16px 22px;margin-top:10px;width:100%;border:none;border-radius:6px;font-size:18px;font-weight:bold;background-color:#556CFF;color:#fff;cursor:pointer}.users-sidebar{padding:20px;width:250px;max-height:100%;overflow-y:auto;border-left:1px solid rgba(159,183,197,0.2);background-color:rgba(100,100,0,0.1)}.users-sidebar__list{padding:0;margin:0;list-style:none}.users-sidebar__item{margin-top:10px;padding:8px 14px;border-radius:3px;font-weight:500;background-color:#fff;box-shadow:0 3px 5px rgba(159,183,197,0.1)}
